const WXAPI = require('apifm-wxapi')
WXAPI.init('tae')
export default {
	data() {
		return {
			hideEyes: false,
			form:{
				username:"",
				pwd:""
			}
		}
	},
	methods: {
		passwordF_B() {
			this.hideEyes = !this.hideEyes;
		},
		formSubmit(e) {
			    wx.login({
			      success: function (res) {
			        const code = res.code; // 微信登录接口返回的 code 参数，下面注册接口需要用到
			        WXAPI.register_simple({
			          code: code
			        }).then(function (res) {
			          // 注册接口返回结果
			          console.log(res)
			        })
			      }
			    })
				uni.showToast({
					title:"注册成功"
				})
				uni.navigateTo({
					url:"../login/login"
				})
			  }
		},
	}
